// app/admin/visualizar/page.tsx
'use client';

/* ===========================================================================
 *  Panel de administración  ―  Visualizar
 * ---------------------------------------------------------------------------
 *  • Vista de tabla conmutables:  productos | usuarios | pedidos
 *  • Barra de filtros rápida por ID
 *  • Edición inline de productos (persistencia en localStorage)
 *  • CRUD básico: update / delete
 *  • Todo client-side, sin llamadas de red
 * ==========================================================================*/

import { useState, useEffect, ChangeEvent } from 'react';
import { Trash2, Funnel } from 'lucide-react';

import { getUsers, getOrders, User, Pedido } from '@/lib/localAuth';

/* -------------------------------------------------------------------------- */
/*                                   Tipos                                    */
/* -------------------------------------------------------------------------- */

type View = 'productos' | 'usuarios' | 'pedidos';

/** Estructura de producto almacenada en localStorage */
export interface Producto {
  id: number;
  nombre: string;
  precio: number;
  imagen: string;
  etiquetas: string[];
  descripcion: string;
  opciones?: {
    color?:  string[];
    sabor?:  string[];
    tamaño?: string[];
  };
  promo?: unknown;
}

/** Etiquetas permitidas para edición rápida */
const TAG_OPTIONS = [
  'bebida', 'frío', 'dulce', 'panadería', 'refresco',
  'snack', 'krusty', 'salado', 'chicle', 'revista',
  'lectura', 'caliente', 'comida',
];

/* -------------------------------------------------------------------------- */
/*                          Helpers de persistencia                           */
/* -------------------------------------------------------------------------- */

/** Recupera la lista de productos del storage (o [] si no existe) */
const getProductos = (): Producto[] =>
  JSON.parse(localStorage.getItem('kwikemart_productos') || '[]');

/* -------------------------------------------------------------------------- */
/*                                Componente                                  */
/* -------------------------------------------------------------------------- */

export default function VisualizarPage() {
  /* ----------------------------- Estados raíz --------------------------- */
  const [view, setView]             = useState<View>('productos');
  const [dropdownOpen, setDropdown] = useState(false);

  /* Filtros */
  const [searchId, setSearchId] = useState('');
  const [orderId,  setOrderId]  = useState('');
  const [userId,   setUserId]   = useState('');

  /* Datos */
  const [productos, setProductos] = useState<Producto[]>([]);
  const [users,     setUsers]     = useState<User[]>([]);
  const [orders,    setOrders]    = useState<Pedido[]>([]);

  /* Edición inline */
  const [editingId, setEditingId] = useState<number | null>(null);
  const [editData,  setEditData]  = useState<Partial<Producto>>({});

  /* ---------------------------- Carga inicial --------------------------- */
  useEffect(() => {
    setProductos(getProductos());
    setUsers(getUsers());
    setOrders(getOrders());
  }, []);

  /* ------------------------- Filtros calculados ------------------------ */
  const filteredProducts = searchId
    ? productos.filter(p => p.id.toString() === searchId)
    : productos;

  const filteredUsers = searchId
    ? users.filter(u => u.id.toString() === searchId)
    : users;

  const filteredOrders = orders
    .filter(o => (orderId ? o.id.toString()     === orderId : true))
    .filter(o => (userId  ? o.userId.toString() === userId  : true));

  /* ------------------- Helpers: edición de productos ------------------- */

  /** Inicia la edición inline de un producto */
  const startEdit = (p: Producto) => {
    setEditingId(p.id);
    setEditData({
      ...p,
      opciones : { ...(p.opciones ?? {}) },
      etiquetas: [...p.etiquetas],
    });
  };

  /** Cancela edición */
  const cancelEdit = () => {
    setEditingId(null);
    setEditData({});
  };

  /** Persiste cambios en localStorage */
  const saveEdit = () => {
    if (!editingId) return;
    const nuevos = productos.map(p =>
      p.id === editingId ? { ...p, ...editData } : p,
    );
    localStorage.setItem('kwikemart_productos', JSON.stringify(nuevos));
    setProductos(nuevos);
    cancelEdit();
  };

  /** Elimina un producto */
  const deleteProducto = (id: number) => {
    if (!confirm('¿Seguro que quieres eliminar este producto?')) return;
    const nuevos = productos.filter(p => p.id !== id);
    localStorage.setItem('kwikemart_productos', JSON.stringify(nuevos));
    setProductos(nuevos);
  };

  /* ------------------------ Manejadores de campos ---------------------- */

  /** Cambia un campo simple del objeto en edición */
  const onChangeField = (field: keyof Producto, val: string) =>
    setEditData(prev => ({ ...prev, [field]: field === 'precio' ? +val : val }));

  /** Cambia imagen (solo guarda nombre de archivo) */
  const onChangeImage = (e: ChangeEvent<HTMLInputElement>) => {
    const file = e.currentTarget.files?.[0];
    if (file) setEditData(prev => ({ ...prev, imagen: file.name }));
  };

  /** Cambia un CSV de opciones */
  const onChangeOption = (key: string, csv: string) =>
    setEditData(prev => ({
      ...prev,
      opciones: { ...(prev.opciones ?? {}), [key]: csv.split(',').map(v => v.trim()) },
    }));

  /** Añade / quita etiqueta */
  const toggleTag = (tag: string) =>
    setEditData(prev => {
      const ets = prev.etiquetas ?? [];
      return { ...prev, etiquetas: ets.includes(tag) ? ets.filter(t => t !== tag) : [...ets, tag] };
    });

  /* ================================ UI ================================= */

  return (
    <section className="relative bg-[#e9d29a] p-6 pb-32">
      {/* ---------------------------- Intro bubble ------------------------ */}
      <div className="relative mb-6 mt-8 w-full">
        <div className="relative w-[80%] rounded-lg border-2 border-blue-400 bg-white px-8 py-4 shadow-md">
          <p className="text-lg font-semibold text-gray-800">
            ¡Por la gracia de los Squishees! Inspecciona usuarios, pedidos y
            productos… tan rápido como sirvo un Squishee de cereza.
          </p>
          {/* punta de bocadillo */}
          <div className="absolute -bottom-[10px] left-6 h-0 w-0 border-l-[5px] border-l-transparent border-r-[20px] border-r-transparent border-t-[10px] border-t-blue-400" />
          <div className="absolute -bottom-[8px] left-[calc(1rem+1px)] h-0 w-0 border-l-[5px] border-l-transparent border-r-[20px] border-r-transparent border-t-[8px] border-t-white" />
        </div>
      </div>

      {/* ---------------------------- Barra filtros ------------------------ */}
      <FiltersBar
        {...{
          view,
          dropdownOpen,
          setDropdown,
          setView,
          searchId,
          setSearchId,
          orderId,
          setOrderId,
          userId,
          setUserId,
        }}
      />

      {/* ---------------------------- Tabla productos ---------------------- */}
      {view === 'productos' && (
        <ProductsTable
          productos={filteredProducts}
          tagOptions={TAG_OPTIONS}
          {...{
            editingId,
            editData,
            startEdit,
            cancelEdit,
            saveEdit,
            deleteProducto,
            onChangeField,
            onChangeImage,
            onChangeOption,
            toggleTag,
          }}
        />
      )}

      {/* ---------------------------- Tabla usuarios ---------------------- */}
      {view === 'usuarios' && <UsersTable users={filteredUsers} />}

      {/* ---------------------------- Tabla pedidos ----------------------- */}
      {view === 'pedidos' && <OrdersTable orders={filteredOrders} />}
    </section>
  );
}

/* ----------------------------------------------------------------------- */
/*                  Sub-componentes (definidos in-file)                    */
/* ----------------------------------------------------------------------- */

/* Barra de filtros y selector de vista */
function FiltersBar(props: {
  view: View;
  dropdownOpen: boolean;
  setDropdown: (b: boolean) => void;
  setView: (v: View) => void;
  searchId: string;
  setSearchId: (s: string) => void;
  orderId: string;
  setOrderId: (s: string) => void;
  userId: string;
  setUserId: (s: string) => void;
}) {
  const {
    view, dropdownOpen, setDropdown, setView,
    searchId, setSearchId, orderId, setOrderId, userId, setUserId,
  } = props;

  return (
    <div className="mb-6 flex items-center justify-center space-x-6">
      <span className="font-semibold">Filtrar por:</span>

      {/* Dropdown selector vistas */}
      <div className="relative inline-block">
        <button
          onClick={() => setDropdown(!dropdownOpen)}
          className="flex items-center gap-2 rounded-full border-2 border-green-300 bg-white px-5 py-3 shadow hover:bg-gray-100"
        >
          <Funnel size={16} /> Abrir filtros
        </button>

        {dropdownOpen && (
          <ul className="absolute z-20 mt-2 w-40 rounded border bg-white shadow-lg">
            {(['productos', 'usuarios', 'pedidos'] as View[]).map((v) => (
              <li key={v}>
                <button
                  onClick={() => {
                    setView(v);
                    setDropdown(false);
                    setSearchId('');
                    setOrderId('');
                    setUserId('');
                  }}
                  className={`block w-full px-5 py-3 text-left hover:bg-green-200 ${
                    view === v ? 'bg-green-300 font-semibold' : ''
                  }`}
                >
                  {v[0].toUpperCase() + v.slice(1)}
                </button>
              </li>
            ))}
          </ul>
        )}
      </div>

      {/* Inputs de filtro según vista */}
      {view === 'pedidos' ? (
        <>
          <FilterInput label="ID pedido" val={orderId} onChange={setOrderId} />
          <FilterInput label="ID usuario" val={userId} onChange={setUserId} />
        </>
      ) : (
        <FilterInput label="ID" val={searchId} onChange={setSearchId} />
      )}
    </div>
  );
}

/* Input reutilizable */
const FilterInput = ({
  label,
  val,
  onChange,
}: {
  label: string;
  val: string;
  onChange: (v: string) => void;
}) => (
  <div className="flex items-center gap-2">
    <label className="font-semibold">{label}:</label>
    <input
      type="text"
      value={val}
      onChange={(e) => onChange(e.target.value)}
      placeholder="..."
      className="rounded-full border-2 border-green-300 bg-white px-5 py-3 shadow-sm focus:ring-2 focus:ring-green-400"
    />
  </div>
);

/* ---------------------------- Tabla productos -------------------------- */
function ProductsTable(props: {
  productos: Producto[];
  tagOptions: string[];
  editingId: number | null;
  editData: Partial<Producto>;
  startEdit: (p: Producto) => void;
  cancelEdit: () => void;
  saveEdit: () => void;
  deleteProducto: (id: number) => void;
  onChangeField: (k: keyof Producto, v: string) => void;
  onChangeImage: (e: ChangeEvent<HTMLInputElement>) => void;
  onChangeOption: (k: string, v: string) => void;
  toggleTag: (t: string) => void;
}) {
  const {
    productos, tagOptions, editingId, editData,
    startEdit, cancelEdit, saveEdit, deleteProducto,
    onChangeField, onChangeImage, onChangeOption, toggleTag,
  } = props;

  return (
    <div className="overflow-auto">
      <h2 className="mb-2 text-xl font-semibold">Productos</h2>

      <table className="min-w-full rounded bg-white shadow">
        <thead className="bg-gray-100">
          <tr>
            {['ID','Imagen','Nombre','Precio','Descripción','Etiquetas','Opciones','Acciones']
              .map((h) => <th key={h} className="px-4 py-2">{h}</th>)}
          </tr>
        </thead>

        <tbody>
          {productos.map((p) => {
            const isEditing = editingId === p.id;
            return (
              <tr key={p.id} className="border-t">
                {/* ID */}
                <td className="px-4 py-2">{p.id}</td>

                {/* Imagen */}
                <td className="px-4 py-2">
                  {isEditing ? (
                    <input type="file" accept="image/*" onChange={onChangeImage} />
                  ) : (
                    <img src={p.imagen} alt={p.nombre} className="h-12 w-12 rounded object-contain" />
                  )}
                </td>

                {/* Nombre */}
                <td className="px-4 py-2">
                  {isEditing ? (
                    <textarea
                      rows={2}
                      className="w-full rounded border px-2 py-1"
                      value={editData.nombre ?? ''}
                      onChange={(e) => onChangeField('nombre', e.target.value)}
                    />
                  ) : (
                    p.nombre
                  )}
                </td>

                {/* Precio */}
                <td className="px-4 py-2 text-right">
                  {isEditing ? (
                    <input
                      type="number"
                      className="w-full rounded border px-2 py-1 text-right"
                      value={editData.precio?.toString() ?? ''}
                      onChange={(e) => onChangeField('precio', e.target.value)}
                    />
                  ) : (
                    `$${p.precio.toFixed(2)}`
                  )}
                </td>

                {/* Descripción */}
                <td className="px-4 py-2">
                  {isEditing ? (
                    <textarea
                      rows={3}
                      className="w-full rounded border px-2 py-1"
                      value={editData.descripcion ?? ''}
                      onChange={(e) => onChangeField('descripcion', e.target.value)}
                    />
                  ) : (
                    p.descripcion
                  )}
                </td>

                {/* Etiquetas */}
                <td className="px-4 py-2">
                  {isEditing ? (
                    <div className="flex flex-wrap gap-1">
                      {tagOptions.map((tag) => {
                        const sel = editData.etiquetas?.includes(tag);
                        return (
                          <span
                            key={tag}
                            onClick={() => toggleTag(tag)}
                            className={`cursor-pointer rounded-full border px-2 py-1 ${
                              sel ? 'border-green-500 bg-green-300' : 'border-gray-300 bg-white'
                            }`}
                          >
                            {tag}
                          </span>
                        );
                      })}
                    </div>
                  ) : (
                    p.etiquetas.join(', ')
                  )}
                </td>

                {/* Opciones */}
                <td className="px-4 py-2">
                  {isEditing ? (
                    <div className="space-y-2">
                      <small className="text-gray-600">Separa por comas</small>
                      {Object.entries(editData.opciones ?? {}).map(([k, vals]) => (
                        <div key={k} className="flex flex-col">
                          <label className="font-semibold capitalize">{k}:</label>
                          <input
                            className="rounded border px-2 py-1"
                            value={(vals as string[]).join(', ')}
                            onChange={(e) => onChangeOption(k, e.target.value)}
                          />
                        </div>
                      ))}
                    </div>
                  ) : (
                    Object.entries(p.opciones ?? {})
                      .map(([k, vs]) => `${k}: ${(vs as string[]).join(', ')}`)
                      .join(' | ')
                  )}
                </td>

                {/* Acciones */}
                <td className="px-4 py-2 text-center">
                  {isEditing ? (
                    <div className="mt-4 flex flex-col items-center space-y-2">
                      <div className="flex space-x-2">
                        <button onClick={saveEdit} className="rounded bg-green-500 px-3 py-1 text-white hover:bg-green-600">
                          Guardar
                        </button>
                        <button onClick={() => deleteProducto(p.id)} className="rounded bg-red-500 p-2 text-white hover:bg-red-600">
                          <Trash2 size={16} />
                        </button>
                      </div>
                      <button onClick={cancelEdit} className="rounded bg-gray-300 px-3 py-1 text-gray-800 hover:bg-gray-400">
                        Cancelar
                      </button>
                    </div>
                  ) : (
                    <div className="flex items-center justify-center space-x-2">
                      <button
                        onClick={() => startEdit(p)}
                        className="rounded bg-blue-500 px-3 py-1 text-white hover:bg-blue-600"
                      >
                        Editar
                      </button>
                      <button
                        onClick={() => deleteProducto(p.id)}
                        className="rounded bg-red-500 p-2 text-white hover:bg-red-600"
                      >
                        <Trash2 size={16} />
                      </button>
                    </div>
                  )}
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}

/* ------------------------------ Tabla usuarios -------------------------- */
const UsersTable = ({ users }: { users: User[] }) => (
  <div>
    <h2 className="mb-2 text-xl font-semibold">Usuarios</h2>
    <table className="min-w-full rounded bg-white shadow">
      <thead className="bg-gray-100">
        <tr>
          {['ID', 'Nombre', 'Email'].map((h) => (
            <th key={h} className="px-4 py-2 text-left">{h}</th>
          ))}
        </tr>
      </thead>
      <tbody>
        {users.map((u) => (
          <tr key={u.id} className="border-t">
            <td className="px-4 py-2">{u.id}</td>
            <td className="px-4 py-2">{u.username}</td>
            <td className="px-4 py-2">{u.email}</td>
          </tr>
        ))}
      </tbody>
    </table>
  </div>
);

/* ------------------------------ Tabla pedidos --------------------------- */
const OrdersTable = ({ orders }: { orders: Pedido[] }) => (
  <div>
    <h2 className="mb-2 text-xl font-semibold">Pedidos</h2>
    <table className="min-w-full rounded bg-white shadow">
      <thead className="bg-gray-100">
        <tr>
          {['ID pedido', 'ID usuario', 'Producto', 'Cantidad', 'Total'].map((h) => (
            <th key={h} className={`px-4 py-2 ${h === 'Cantidad' || h === 'Total' ? 'text-right' : 'text-left'}`}>
              {h}
            </th>
          ))}
        </tr>
      </thead>
      <tbody>
        {orders.map((o) => (
          <tr key={o.id} className="border-t">
            <td className="px-4 py-2">{o.id}</td>
            <td className="px-4 py-2">{o.userId}</td>
            <td className="px-4 py-2">{o.productName}</td>
            <td className="px-4 py-2 text-right">{o.quantity}</td>
            <td className="px-4 py-2 text-right">${o.total.toFixed(2)}</td>
          </tr>
        ))}
      </tbody>
    </table>
  </div>
);
